* {
  box-sizing: border-box;
}

body {
  line-height: 1.4;
  margin: 0;
  min-height: 100vh;
}

.header {
  padding: 20px;
  background-color: #333;
  color: white;
  text-align: center;
  font-size: 30px;
  font-weight: 700;
  margin-bottom: 10px;
  letter-spacing: 0.1em;
}

.footer {
  padding: 10px;
  background-color: #333;
  color: white;
  text-align: center;
  font-size: 20px;
  font-weight: 700;
  letter-spacing: 0.1em;
  margin-top: 40px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}

.resume-wrapper {
  width: 1000px;
  margin-right: auto;
  margin-left: auto;
  margin-top: 50px;
  margin-bottom: 100px;
  border: 1px solid black;
  box-shadow: 17px 10px 65px -38px rgba(0, 0, 0, 0.75);
}

.left-panel {
  display: flex;
  flex-direction: column;
  background-color: rgb(51, 83, 132);
  color: rgb(243, 245, 248);
}

.left-panel .aside-title {
  align-self: flex-start;
  margin-left: 10px;
  margin-bottom: -5px;
  font-style: italic;
  font-weight: 700;
  font-size: 20px;
  letter-spacing: 0.1em;
}

.skills-wrapper:hover #add-new-skill-button {
  opacity: 1;
  margin: 0 auto;
  display: block;
}

.skills-wrapper-items {
  display: block;
  margin-top: 0px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
}

#add-new-skill-button {
  opacity: 0;
}

.edit-img-button {
  width: 10px;
  filter: invert(1);
}

.skill-edit-form {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.edit-button-wrapper {
  width: 80%;
  display: flex;
  justify-content: space-around;
}

.skill-edit-form input {
  width: 80%;
  margin-bottom: 10px;
}

.skill-item {
  display: grid;
  grid-template-columns: auto 20px 20px;
  align-items: center;
}

.skill-item li {
  margin: 10px;
}

.skill-item img {
  opacity: 0;
  cursor: pointer;
}

.skill-item:hover img {
  opacity: 1;
}

#experience,
#education {
  background-color: whitesmoke;
  position: relative;
}

#experience h1,
#education h1 {
  border-top: 1px solid #456b53;
  border-bottom: 2px solid #456b53;
  font-size: 20px;
  padding: 5px;
}

#general-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

#resume {
  display: grid;
  grid-template-columns: 250px auto;
}

#locationLogo {
  width: 20px;
  height: 20px;
  margin-right: 5px;
}

.info-wrapper {
  display: flex;
  align-items: center;
}

.general-info-wrapper {
  display: flex;
  flex-direction: column;
  margin-top: 5px;
}

#profile-photo {
  border-radius: 90%;
  width: 200px;
}

#edit-skill-button {
  margin-left: 50px;
}

#generalinfo-edit {
  position: absolute;
  display: flex;
  flex-direction: column;
  right: -100%;
  top: 50%;
  background-color: rgb(16, 41, 78);
  width: 200px;
  line-height: 1.8;
  padding: 20px;
  border: 1px solid black;
  z-index: 1;
}

.intro-form {
  position: absolute;
  display: flex;
  flex-direction: column;
  right: 10%;
  top: -10px;
  background-color: rgb(16, 41, 78);
  color: white;
  width: 200px;
  line-height: 1.8;
  padding: 20px;
  border: 1px solid black;
}

#generalinfo-edit #close-edit-info-button {
  width: 50px;
  align-self: center;
  margin-top: 20px;
}

#edit-info-button {
  position: absolute;
  top: 50%;
  right: 10px;
  opacity: 0;
}

#general-info:hover #edit-info-button {
  opacity: 1;
}

#general-info p,
.skills-wrapper li {
  font-size: 13px;
}

.edit-form {
  position: absolute;
  display: flex;
  flex-direction: column;
  border: 1px solid black;
  z-index: 10;
  right: -30%;
}

.add-education {
  background-color: black;
  color: white;
  padding: 20px;
  line-height: 1.8;
}

.add-experience {
  background-color: black;
  color: white;
  padding: 20px;
  line-height: 1.8;
}

.buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.new-skill-form {
  display: block;
  text-align: center;
  margin-top: 4px;
}

.buttons-skill-form {
  display: flex;
  justify-content: space-evenly;
  margin-top: 4px;
}

.experience-wrapper {
  display: grid;
  grid-template-columns: 200px auto 40px;
  grid-column-gap: 40px;
  margin-top: 20px;
}

ul li {
  list-style-type: none;
}

#position {
  font-weight: 700;
  color: #456b53;
  font-size: 18px;
  text-align: right;
  letter-spacing: 0.1em;
}

#date-from-until {
  text-transform: uppercase;
  letter-spacing: 0.1em;
  margin-bottom: 5px;
  text-align: right;
  font-size: smaller;
}

#companyName {
  font-style: italic;
  font-weight: 700;
  font-size: 20px;
}

#city {
  color: #666;
  font-style: italic;
  font-size: smaller;
}

#mainTasks {
  font-size: smaller;
}

.x-button {
  background: transparent;
  box-shadow: 0px 0px 0px transparent;
  border: 0px solid transparent;
  text-shadow: 0px 0px 0px transparent;
  cursor: pointer;
  align-self: flex-start;
  justify-content: space-around;
  margin-top: 5px;
  opacity: 0;
}

.experience-wrapper:hover .x-button {
  opacity: 1;
}

#add-new-exp-button,
#edit-intro,
#add-new-edu-button {
  opacity: 0;
  position: absolute;
  top: 45px;
  right: 10px;
}

#experience:hover #add-new-exp-button {
  opacity: 1;
}

#education:hover #add-new-edu-button {
  opacity: 1;
}

#title-name {
  font-family: questa-grande, serif;
  font-weight: 700;
  font-size: 40px;
  font-style: normal;
  color: rgb(51, 83, 132);
  letter-spacing: 0.1em;
}

#title-title,
#personal-profile {
  font-family: questa-grande, serif;
  font-weight: 500;
  font-style: normal;
  color: rgb(51, 83, 132);
  letter-spacing: 0.1em;
}

#personal-profile-text {
  font-size: 13px;
  line-height: 1.8;
}

.introduction-wrapper {
  margin-left: 40px;
  margin-right: 40px;
  position: relative;
}

.introduction-wrapper:hover #edit-intro {
  opacity: 1;
}

.main-title {
  margin-left: 20px;
  color: #456b53;
  font-weight: 900;
}

button {
  cursor: pointer;
}
